import global from './../../global'

const EnemyState = {
    invalid: -1,
    run: 1,
    tipsBegan: 2,
    tipsEnd: 3

};
cc.Class({
    extends: cc.Component,

    properties: {},

    onLoad() {

        this.state = EnemyState.invalid;
        this.setState(EnemyState.run);
        this.node.on('fly', () => {
            console.log('');
            let action1 = cc.bezierTo(1, [cc.p(78, 9), cc.p(86, 302), cc.p(418, 319)]);
            this.node.runAction(cc.sequence(action1, cc.callFunc(() => {
                this.node.dead = true;
            })));
            let raaction = cc.rotateTo(1, 360 * 4);
            this.node.runAction(raaction);
            this.setState(EnemyState.tipsEnd);
            let scal1 = cc.scaleTo(1, 0.2, 0.2);
            this.node.runAction(scal1);
            this.node.isFly = true;

        });

        // global.account.gameCtl.onPause(() => {
        //     this.node.emit('time-scale', 0);
        // });
        // global.account.gameCtl.onResume(() => {
        //     this.node.emit('time-scale', defines.timeScale);
        // });

    },
    update() {
        if (this.state === EnemyState.run && !global.account.gameCtl.isQuick()) {
            let dis = cc.pDistance(this.node.position, global.account.playerData.playerNode.position);
            let speedX=this.node.getComponent("stub").speedX;
            if (dis/speedX<2.4) {
                this.setState(EnemyState.tipsBegan);
            }
        }

    },
    setState(state) {
        if (this.state === state) {
            return
        }
        switch (state) {
            case EnemyState.tipsBegan:
                global.account.gameCtl.tipsBegan();
                break;
            case EnemyState.tipsEnd:
                break;
            default:
                break;
        }
        this.state = state;
    },
    onDestroy() {
        console.log('');
    }
});
