import global from "../global";

cc.Class({
    extends: cc.Component,

    properties: {

        gameNode: {
            default: null,
            type: cc.Node
        },
        stubPrefab: {
            default: null,
            type: cc.Prefab
        },
        prizePrefab: {
            default: null,
            type: cc.Prefab
        },
        enemyPrefabList: {
            default: [],
            type: cc.Prefab
        },
        initPosNode: {
            default: null,
            type: cc.Node
        },
        initPrizePosNodeList: {
            default: [],
            type: cc.Node
        },
        playerNode:cc.Node
    },
    onLoad() {
        this.player=this.playerNode.getComponent("player");
        this.stubList = [];

        this.addStubIndex = 0;

        this.prizeList = [];

        this.enemyList = [];


        this.coinList = [];

        this.addStub();


        //
        this.addCoinFlag = 0;


        this.addCoinFlagDurection = 4;


        this.addCoinDurection = 0;

        global.account.gameCtl.onPause(() => {
            for (let i = 0; i < this.stubList.length; i++) {
                let node = this.stubList[i];
                node.emit('time-scale', 0);
            }

            for (let i = 0; i < this.coinList.length; i++) {
                let coin = this.coinList[i];
                coin.emit('pause');
            }
        });
        global.account.gameCtl.onResume(() => {
            for (let i = 0; i < this.stubList.length; i++) {
                let node = this.stubList[i];
                console.log('on resume = ' + defines.timeScale);
                node.emit('time-scale', defines.timeScale);
            }
            for (let i = 0; i < this.coinList.length; i++) {
                let coin = this.coinList[i];
                coin.emit('resume');
            }
        });
        global.account.gameCtl.onQuick(() => {
            for (let i = 0; i < this.stubList.length; i++) {
                let node = this.stubList[i];
                node.emit('quick');
            }
            for (let i = 0; i < this.coinList.length; i++) {
                let coin = this.coinList[i];
                coin.emit('quick');
            }
        });
        global.account.gameCtl.onQuickEnd(() => {
            for (let i = 0; i < this.stubList.length; i++) {
                let node = this.stubList[i];
                node.emit('quick-end');
            }
            for (let i = 0; i < this.coinList.length; i++) {
                let coin = this.coinList[i];
                coin.emit('quick-end');
            }
        });
        global.account.gameCtl.onTimeScale(() => {
            for (let i = 0; i < this.stubList.length; i++) {
                let node = this.stubList[i];
                node.emit('time-scale', defines.timeScale);
            }
        });

        this.initPool();

    },
    initPool() {
        this.coinPool = new cc.NodePool();
        let initCount = 6;
        for (let i = 0; i < initCount; i++) {
            let coin = cc.instantiate(this.prizePrefab);
            this.coinPool.put(coin);
        }
    },
    update(dt) {

        for (let i = 0; i < this.stubList.length; i++) {
            let enemy = this.stubList[i];
            if (enemy.dead === true) {
                this.stubList.splice(i, 1);
                enemy.destroy();
            }
        }


        for (let i = 0; i < this.coinList.length; i++) {
            let coin = this.coinList[i];
            if (coin.x < cc.winSize.width * -0.5){
                this.coinPool.put(coin);
                this.coinList.splice(i, 1);
            }
        }


        if (this.stubList.length !== 0 && this.stubList[this.stubList.length - 1].position.x < cc.winSize.width * 0.2) {
            if (this.addStubIndex === 3) {
                this.addStubIndex = 0;
                this.addEnemy();
            } else {
                this.addStub();
            }
        }


        this.addCoinFlagDurection -= dt;
        if (this.addCoinFlagDurection <= 0 && this.addCoinFlag === 0) {
            this.addCoinFlagDurection = 4;
            this.addCoinFlag = Math.floor(Math.random() * 3 + 2);
        }
        if (this.addCoinFlag > 0) {
            if (this.coinList.length === 0){
                this.addCoin();
            }else {

                let maxX = -10000;
                for (let i = 0 ; i < this.coinList.length ; i ++){
                    if (this.coinList[i].x > maxX){
                        maxX = this.coinList[i].x;
                    }
                }
                if (this.initPrizePosNodeList[0].x - maxX > 300){
                    this.addCoin();
                }
            }

        }


        //
        // console.log('stub = ' + this.stubList.length);
    },
    addCoin() {
        this.addCoinFlag--;
        let node = this.initPrizePosNodeList[Math.floor(Math.random() * this.initPrizePosNodeList.length)];
        let coin = null;
        if (this.coinPool.size() > 0) {
            console.log('');
            coin = this.coinPool.get();
        } else {
            console.log('');
            coin = cc.instantiate(this.prizePrefab);
        }
        coin.zIndex = 0;
        coin.parent = this.gameNode;
        coin.position = node.position;
        coin.scale = 1;
        if (coin.getComponent('stub')) {
            coin.getComponent('stub').initWithData({speed: this.player.speeds[this.player.speedLevel], isQuick: global.account.gameCtl.isQuick(), manager: this});

        }

        coin.dead = false;
        this.coinList.push(coin);
    },
    addStub() {
        let stub = this.createStub(this.initPosNode, this.stubPrefab);
        this.stubList.push(stub);
        this.addStubIndex++;
    },
    addPrize() {
        let stub = this.createStub(this.initPrizePosNodeList[Math.floor(Math.random() * this.initPrizePosNodeList.length)], this.prizePrefab);
        stub.zIndex = 2;
        this.prizeList.push(stub);
    },
    addEnemy() {
        let enemy = this.createStub(this.initPosNode, this.enemyPrefabList[0]);
        enemy.emit('init-manager', this);
        // this.enemyList.push(enemy);
        // console.log('add enemy');
        this.stubList.push(enemy);
    },
    createStub(initPos, prefab) {
        let stub = cc.instantiate(prefab);
        stub.zIndex = 0;
        stub.parent = this.gameNode;
        stub.position = initPos.position;
        if (stub.getComponent('stub')) {
            stub.getComponent('stub').initWithData({speed: this.player.speeds[this.player.speedLevel], isQuick: global.account.gameCtl.isQuick()});
        }
        return stub
    }

});
