import global from './../global'
cc.Class({
    extends: cc.Component,

    properties: {
        bgList: {
            default: [],
            type: cc.Node
        },
        bgSpeed: 0,
        playerNode:cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.oldBgSpeed = this.bgSpeed;
        global.account.gameCtl.onQuick(()=>{
           //
            console.log('');
           this.bgSpeed += defines.addSpeed;
        });
        global.account.gameCtl.onQuickEnd(()=>{
            this.bgSpeed = this.oldBgSpeed;
        })
    },
    update(dt){

        if (global.account.gameCtl.isRunning()){
            let com=this.playerNode.getComponent("player");
            let speed=com.speeds[com.speedLevel];

            for (let i = 0 ; i < this.bgList.length ; i ++){
                // this.bgList[i].position = cc.p(this.bgList[i].position.x - this.bgSpeed * dt * defines.timeScale, this.bgList[i].position.y);
                this.bgList[i].x -=  speed* dt * defines.timeScale;
            }
            for (let i = 0; i < this.bgList.length; i++) {
                if (this.bgList[i].position.x < -this.bgList[i].width) {
                    let index = i + 1;
                    if (index > this.bgList.length - 1) {
                        index = 0;
                    }
                    // this.bgList[i].position = cc.p(this.bgList[index].position.x + this.bgList[i].width, this.bgList[i].position.y);
                    this.bgList[i].x = this.bgList[index].x + this.bgList[i].width;
                }
            }
        }
    }
});
