import global from './../global'

cc.Class({
    extends: cc.Component,

    properties: {
        totalTime: 0,
        player: {
            default: null,
            type: cc.Node
        },
        runProgress: {
            default: null,
            type: cc.Node
        },
        upButton: {
            default: null,
            type: cc.Node
        },
        runButton: {
            default: null,
            type: cc.Node
        },
        runActiveButton: {
            default: null,
            type: cc.Node
        },
        runButtonProgress: {
            default: null,
            type: cc.ProgressBar
        },
        coinCountLabel: {
            default: null,
            type: cc.Label
        },
        distanceNode: {
            default: null,
            type: cc.Node
        },
        pauseLayer: {
            default: null,
            type: cc.Prefab
        },
        runButtonIcon: {
            default: null,
            type: cc.Node
        }
    },


    onLoad() {
        this.currentTime = 0;
        // global.account.gameCtl.onPause(()=>{
        //    //
        //    this.upButton.opacity = 100;
        //    this.runButton.opacity = 100;
        // });
        let self=this;
        global.account.gameCtl.event.on("speed-changed",function(opt){
            self.runButtonProgress.progress=opt.currentLevel/opt.maxLevel;
        });
        global.account.gameCtl.onTipsBegan((data) => {
            console.log(' = ' + JSON.stringify(data));
            this.upButton.opacity = 100;
            this.runButton.opacity = 100;
        });
        // global.account.gameCtl.onTipsEnd((data)=>{
        //     console.log('' + JSON.stringify(data));
        //     this.upButton.opacity = 255;
        //     this.runButton.opacity = 255;
        // });
        global.account.gameCtl.onAttackEnd(() => {
            //
            this.upButton.opacity = 255;
            this.runButton.opacity = 255;
        });
        global.account.gameCtl.onDistanceChange((distance)=>{
            this.distanceNode.emit('distance-change', distance);
        });



        cc.eventManager.addListener({
            event: cc.EventListener.KEYBOARD,
            onKeyPressed: (keyCode, event) => {
                this._keyHandler(keyCode, true);
            },
            onKeyReleased: (keyCode, event) => {
                this._keyHandler(keyCode, false);
            },
        }, this.node);


        this.isCanRun = false;
        this.runButtonProgressSpeed = 0.01;
        this.runButtonIcon.active = false;

    },
    _keyHandler(code, isDown){
        switch (code){
            case cc.KEY.up:
            case cc.KEY.space:
                console.log('');
                if (this.upButton.opacity === 255){
                    this.player.emit('jump');
                }
                break;
            case cc.KEY.enter:
                console.log('');
                // this.player.emit('quick');
                if (this.isCanRun){
                    global.account.gameCtl.quick();
                }
                break;
            default:
                break;
        }
    },
    onButtonClick: function (event, customData) {
        switch (customData) {
            case 'up-button':
                //
                if (this.upButton.opacity === 255) {
                    this.player.emit('jump');
                }
                break;
            case 'run-button':
                if (this.runButton.opacity === 255 && this.isCanRun) {
                    console.log('run button');
                    global.account.gameCtl.quick();
                    this.runButtonIcon.active = false;


                }
                break;
            case 'pause':
                let node = cc.instantiate(this.pauseLayer);
                node.parent = this.node;
                global.account.gameCtl.pause();
                break;
            default:
                break;
        }
    },
    update(dt) {


        if (global.account.gameCtl.isRunning()){
            this.currentTime += dt;
            this.runProgress.getComponent('mask-progress').setProgress((this.totalTime - this.currentTime) / this.totalTime);
            this.coinCountLabel.string = global.account.playerData.goldCount + '';


            // if (global.account.gameCtl.isQuick()){
            //     if (this.runButtonProgress.progress > 0){
            //         this.runButtonProgress.progress -= this.runButtonProgressSpeed;
            //         if (this.runButtonProgress.progress <= 0){
            //             this.runButtonProgress.progress = 0;
            //             this.isCanRun = false;
            //             global.account.gameCtl.qucikEnd();

            //         }
            //     }
            // }else {
            //     if (this.runButtonProgress.progress < 1){
            //         this.runButtonProgress.progress += this.runButtonProgressSpeed;
            //         if (this.runButtonProgress.progress >= 1){
            //             this.runButtonProgress.progress = 1;
            //             console.log('');
            //             this.isCanRun = true;
            //             this.runButtonIcon.active = true;

            //         }
            //     }
            // }

        }
    }


});
