
cc.Class({
    extends: cc.Component,

    properties: {

    },
    onLoad(){
        this.node.dead = false;
        this.node.on('fly', ()=>{
            console.log('');
            let action = cc.bezierTo(0.4, [this.node.position,cc.p(this.node.x, this.node.y + 200), cc.p(-530,269)]);
            this.node.runAction(cc.sequence(action, cc.callFunc(()=>{
                console.log('');
                // this.node.dead = true;
                this.node.x = -1000;
            })));
            this.node.runAction(cc.scaleTo(0.4, 0.2,0.2));
        });
        this.node.on('pause',()=>{
           //
           this.node.getComponent(cc.Animation).pause();
        });
        this.node.on('resume',()=>{
           //
            this.node.getComponent(cc.Animation).resume();
        });
    },
    onEnable(){
        console.log("on enable");
    },
    onDestroy(){
        console.log('');
    }
});
